[type="checkbox"]:checked, [type="checkbox"]:not(:checked), [type="radio"]:checked, [type="radio"]:not(:checked) {
  width: 0;
  height: 0;
  visibility: hidden;
  position: absolute;
  left: -9999px;
}

.checkbox:checked + label, .checkbox:not(:checked) + label {
  width: 70px;
  text-align: center;
  height: 6px;
  background-image: linear-gradient(298deg, var(--red), var(--yellow) );
  border-radius: 4px;
  margin: 0 auto;
  padding: 0;
  display: inline-block;
  position: relative;
}

.checkbox:checked + label:before, .checkbox:not(:checked) + label:before {
  cursor: pointer;
  z-index: 2;
  text-align: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-family: unicons;
  font-size: 20px;
  line-height: 40px;
  transition: all .3s linear;
  position: absolute;
  top: -17px;
}

.checkbox:not(:checked) + label:before {
  content: url("sun-summertime-svgrepo-com.4a3827af.svg");
  color: var(--grey);
  background-color: var(--red);
  left: 0;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15), 0 0 0 1px rgba(26, 53, 71, .07);
}

.checkbox:checked + label:before {
  content: url("moon-svgrepo-com.3c25f7f7.svg");
  color: var(--yellow);
  background-color: var(--dark-light);
  left: 30px;
  box-shadow: 0 4px 4px rgba(26, 53, 71, .25), 0 0 0 1px rgba(26, 53, 71, .07);
}

